  private davariable(cad:string,varx:string):string {
    var re="";
     var otro=cad
     var bb:any=[]
     if(otro.indexOf(varx)>0)
     {
     bb=otro.split(varx)


       if(bb.length==0)
       {
         re="error"
       }
       else{

         var cc:any=bb[1].split(';')
         var dd:any=cc[1]
         var ee:any=dd.split('&')
         re=ee[0];

       }
      }
      else
      {
        re="error"
      }

     return re;
   }

    letras="abcdefghyjklmnñopqrstuvwxyz";

    tiene_letras(texto):number {
      texto = texto.toLowerCase();
      for(var i=0; i<texto.length; i++){
         if (this.letras.indexOf(texto.charAt(i),0)!=-1){
            return 1;
         }
      }
      return 0;
   }


